.map-contener {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  overflow: hidden;

  .leaflet-container {
    width: 100%;
    height: 100%;
    z-index: 1;

    .leaflet-popup-content {
      h3 {
        margin: 0;
        color: #5c9f61;
        text-align: center;
      }
      div{
        margin: 5px 0px;
      }
      p{
        margin: 0;
      }
    }
  }
}

.color-yellow {
  background: rgb(242, 205, 81);
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(242, 205, 81, 0.8);
  border: 1px solid rgba(132, 132, 132, 0.4);
}
.color-green {
  background: rgb(92, 159, 97);
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(92, 159, 97, 0.8);
  border: 1px solid rgba(132, 132, 132, 0.4);
}
.color-red {
  background: rgb(194, 83, 72);
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(194, 83, 72, 0.8);
  border: 1px solid rgba(132, 132, 132, 0.4);
}

.legend-container {
  width: 200px;
  height: 200px;
  padding: 5px;
  color: black;
  position: absolute;
  font-size: 15px;
  top: 5px;
  left: 5px;
  z-index: 500;
  height: auto;
  width: auto;
  border-radius: 4px;
  background-color: rgba(220, 220, 220, 0.8);

  .legend-container-wrapper-inform {
    background: white;
    padding: 15px;
    border-radius: 4px;
    user-select: none;

    p {
      margin: 0 0 5px 0;
    }

    h3 {
      margin: 0;
      padding: 0;
      color: #5c9f61;
      margin-bottom: 10px;
    }
    .legend-information-container {
      display: flex;
      flex-direction: column;
      .item-legend {
        display: flex;
        flex-direction: row;
        .circle-legend {
          margin-right: 10px;
          width: 20px;
          height: 20px;
        }
      }
    }
  }
}
